canvas {
    width: 860px;
    height: 640px;
    margin: auto;
    display: block;
    background: rgb(0, 0, 0);
}
.canvas {
    position: absolute;
    background: #000;
    vertical-align: middle;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
}
p {
    font-size: 13px;
    line-height: 24px;
}
#root,
html,
body,
.tip {
    position: relative;
    z-index: 2;
    background: #fff;
    height: 100%;
    left: 0;
    top: 0;
    box-sizing: border-box;
}
.tipButton {
    background: #9a9b92;
    text-decoration: none;
    padding: 4px;
    position: fixed;
    z-index: 2;
    right: 32px;
    top: 32px;
}

    .bb-bookblock {
        width: 300px;
        /* height: 225px; */
    }

    ul.bb-custom-grid {
        list-style: none;
        margin: 0px auto 30px auto;
        padding: 40px 0 0 0;
        width: 700px;
    }

    /* Micro clearfix hack by Nicolas Gallagher http://nicolasgallagher.com/micro-clearfix-hack/ */
    ul.bb-custom-grid:before,
    ul.bb-custom-grid:after {
        content: " ";
        display: table;
    }

    ul.bb-custom-grid:after {
        clear: both;
    }

    /* end clearfix hack */

    ul.bb-custom-grid li {
        float: left;
        /* width: 300px;
        height: 260px; */
        padding: 10px 20px 50px 20px;
        background: #e9e8e8;
        position: relative;
        -webkit-box-sizing: content-box;
        -moz-box-sizing: content-box;
        box-sizing: content-box;
        margin-bottom: 20px;
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    }

    ul.bb-custom-grid li:nth-child(odd) {
        margin-right: 20px;
    }

    ul.bb-custom-grid li:before,
    ul.bb-custom-grid li:after {
        content: '';
        position: absolute;
        z-index: -2;
        bottom: 15px;
        left: 10px;
        width: 50%;
        height: 20%;
        box-shadow: 0 15px 10px rgba(0, 0, 0, 0.7);
        -webkit-transform: rotate(-3deg);
        -moz-transform: rotate(-3deg);
        -ms-transform: rotate(-3deg);
        -o-transform: rotate(-3deg);
        transform: rotate(-3deg);
    }

    ul.bb-custom-grid li:after {
        right: 10px;
        left: auto;
        -webkit-transform: rotate(3deg);
        -moz-transform: rotate(3deg);
        -ms-transform: rotate(3deg);
        -o-transform: rotate(3deg);
        transform: rotate(3deg);
    }

    ul.bb-custom-grid li a {
        display: block;
    }

    ul.bb-custom-grid li h3 {
        color: #9a9b92;
        margin: 0;
        padding: 0;
        text-transform: uppercase;
        font-family: "Montserrat", sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 35px;
        border-top: 1px dashed rgba(0, 0, 0, 0.1);
        text-shadow: 0 1px 1px rgba(255, 255, 255, 0.9);
    }

    ul.bb-custom-grid nav {
        text-align: center;
        margin-top: 12px;
        padding-bottom: 5px;
        border-bottom: 1px dashed rgba(0, 0, 0, 0.1);
    }

    ul.bb-custom-grid nav span {
        display: inline-block;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background: #f0f0f0;
        margin: 3px;
        cursor: pointer;
        box-shadow:
            inset 0 1px 1px rgba(0, 0, 0, 0.2),
            0 2px 1px rgba(255, 255, 255, 0.9);
    }

    ul.bb-custom-grid nav span.bb-current {
        background: #bdd70d;
    }